  background-color: #e0e6ea;
}

.detail-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #e0e6ea; 
}

.navbar {
  margin-top: -10px;
  width: 100%; 
  height: 40px;
  display: flex;
  justify-content: flex-start;
  padding: 10px;
  background-color: #e0e6ea;
}

.back-button {
  margin-left: -10px;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center;
  background-size: contain;
  border: none;
  width: 33px; 
  height: 33px; 
  cursor: pointer;
  border-radius: 90px;
  
}

.image-container {
  border-radius: 8px;
  margin-top: 10px; 
  width: 400px; 
  height: 400px; 
  display: flex;
  justify-content: center; 
  align-items: center;
  overflow: hidden; 
  border-radius: 8px;
}

.product-image {
  width: 100%; 
  height: auto; 
  object-fit: contain; 
}

.product-info {
  margin-top:-3px;
  width: 100%;
  padding: 20px;
  color: #555657;
}

.product-info h1 {
  margin-top: 8px;
  text-align: left;
  font-family: 'pm';
  font-size: 15pt;
  font-weight: bold;
  color: #555657;
}

.product-info p {
  margin-top: 10px;
  text-align: right;
  font-family: 'pm';
  font-size: 12pt;
  color: #555657;
}

.tactile-info-container {
  margin-top: -30px;
}


.view-product-button {
  margin-top: 25px;
  background-color: #FFDBDA;
  color: #555657;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  width: 180px;
  border-radius: 12px;
  font-family: 'pr';
  border: 0.2px solid rgba(0, 0, 0, 0.7);
}

.view-product-button:hover {
  background-color: #FFFFFF;
  color: #000000;
}



@font-face {
  font-family: 'pm';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
}
@font-face {
  font-family: 'pr';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format('truetype');
